import React from 'react';

class Input extends React.Component {
    constructor() {
        super()
        this.state = {
            "lastSearch": "",
            "result": ""
        }
        this.handleClick = this.handleClick.bind(this)
    }
    handleClick() {
        const input = document.getElementById("input")
        if (input.value < 0) {
            alert("Please put a keyword!")
            return
        }
        this.setState({
            "lastSearch": `${input.value}`,
            "result": `https://www.youtube.com/results?search_query=${input.value}`
        })
    }
    render() {
        return (
            <div>
                <input id="input" type="text" />
                <button onClick={this.handleClick}>Search!</button>
                <h3>View results <a href={this.state.result} target="_blank">here</a></h3>
                <h5>Last Search was {this.state.lastSearch}</h5>
            </div>
        )
    }
}

export default Input